
import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { AppRoutingModule } from './app-routing.module';
import { RouterModule, Routes } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MainpageComponent } from '../app/components/mainpage/mainpage.component';
import { LoginComponent } from '../app/components/login/login.component';
import { PagenotfoundComponent } from '../app/components/pagenotfound/pagenotfound.component';
import { HomeComponent } from '../app/components/home/home.component';
import { TeacherComponent } from '../app/components/teacher/teacher.component';
import { DemoMaterialModule } from './material-module';
import { FormsModule } from '@angular/forms';
import { ReactiveFormsModule } from '@angular/forms';
import { PermissionComponent } from './components/permission/permission.component';
import { DialogAddPermissionDialog } from './dialog/addpermission/addpremission.dialog';
import { GradesComponent } from './components/grades/grades.component';
import { DialogAddClassDialog } from './dialog/addclass/addclass.dialog';
import { DialogAddTeacherDialog } from './dialog/addteacher/addteacher.dialog';
import { DialogAddStudentDialog } from './dialog/addstudent/addstudent.dialog';
import { PrincipalComponent } from './components/principal/principal.component';
import { DialogDeleteDialog } from './dialog/deletebox/delete.dialog';
import { StudentlistComponent } from './components/grades/studentlist/studentlist.component';
import { TimetableComponent } from './components/grades/timetable/timetable.component';
import { DialogStudentDetailsDialog } from './dialog/studentDetails/studentdetails.dialog';
import { FlexLayoutModule } from '@angular/flex-layout';
import { StudentresultComponent } from './components/grades/studentresult/studentresult.component';
import { StudentfeesComponent } from './components/grades/studentfees/studentfees.component';
import { DialogTimeTableDialog } from './dialog/addtimetable/timetable.dialog';
import { DialogResultDialog } from './dialog/addresult/result.dialog';
import { DialogAddPricipalDialog } from './dialog/addprincipal/addprincipal.dialog';
import { DialogLogoutConfirmDialog } from './dialog/logoutconfirm/logout.dialog';
const appRoutes: Routes = [
  { path: 'login', component: LoginComponent },
  {
    path: 'main', component: MainpageComponent, children: [
      { path: 'permission', component: PermissionComponent },
      { path: 'home', component: HomeComponent },
      { path: 'teacher', component: TeacherComponent },
      { path: 'principal', component: PrincipalComponent },
      { path: 'grade/:id', component: GradesComponent },
    ]
  },
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  { path: '**', component: PagenotfoundComponent },
  // outlet: 'secondRouter'
];


@NgModule({
  declarations: [MainpageComponent,
    LoginComponent,
    PagenotfoundComponent,
    StudentlistComponent,
    TimetableComponent,
    StudentresultComponent,
    StudentfeesComponent,
    HomeComponent,
    TeacherComponent,
    GradesComponent,
    PrincipalComponent,
    DialogAddTeacherDialog,
    DialogAddStudentDialog,
    DialogStudentDetailsDialog,
    DialogDeleteDialog,
    DialogAddClassDialog,
    DialogAddPermissionDialog,
    DialogTimeTableDialog,
    DialogResultDialog,
    DialogAddPricipalDialog,
    DialogLogoutConfirmDialog,
    PermissionComponent],
  imports: [AppRoutingModule,
    BrowserModule,
    DemoMaterialModule,
    BrowserAnimationsModule,
    FlexLayoutModule,
    ReactiveFormsModule,
    FormsModule
    , RouterModule.forRoot(
      appRoutes,
      { enableTracing: false }
    )
  ],
  providers: [],
  bootstrap: []
})
export class RouteModule { }
