import { Component, Inject, ViewChild, AfterViewInit } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
@Component({
  selector: 'add-timetable',
  templateUrl: 'timetable.dialog.html',
})
export class DialogTimeTableDialog{
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  constructor(public dialogRef: MatDialogRef<DialogTimeTableDialog>,
    @Inject(MAT_DIALOG_DATA) public data,private _formBuilder: FormBuilder ) {

  }
  ngOnInit(): void {
    this.firstFormGroup = this._formBuilder.group({
      firstCtrl: ['', Validators.required]
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
  }
  onNoClick(state): void {
    this.dialogRef.close(state);
  }

}