import { Component, OnInit, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { DialogAddStudentDialog } from 'src/app/dialog/addstudent/addstudent.dialog';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AngularFirestore } from '@angular/fire/firestore';
import { MatTableDataSource } from '@angular/material/table';
import { MatPaginator } from '@angular/material/paginator';
import { DialogDeleteDialog } from 'src/app/dialog/deletebox/delete.dialog';
import { DialogStudentDetailsDialog } from 'src/app/dialog/studentDetails/studentdetails.dialog';
let allStudentData: any[];
@Component({
  selector: 'app-studentlist',
  templateUrl: './studentlist.component.html',
  styleUrls: ['./studentlist.component.css']
})
export class StudentlistComponent implements OnInit {
  displayedColumns: string[] = ['id', 'name', 'mobileNumber', 'edit', 'delete', 'Info'];
  dataSource = new MatTableDataSource<any>(allStudentData);
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  isDataLoaded = false;
  constructor(private route: ActivatedRoute, public dialog: MatDialog, private db: AngularFirestore) { }
  id: any;
  private subscribe: any;
  allStudentData: any[];

  ngOnInit() {
    this.subscribe = this.route.params.subscribe(params => {
      this.id = params['id'];
      this.getstudentData();
    });
  }

  ngOnDestroy() {
    this.subscribe.unsubscribe();
  }
  getstudentData() {
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      this.isDataLoaded = true;
      let value = JSON.parse(localStorage.user_data);

      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).
        doc(value.district).collection(value.schoolName).doc('allClass').collection('Classes').
        doc(this.id).collection('students');
      ref.snapshotChanges().subscribe((res: any) => {
        this.isDataLoaded = false;
        if (res) {
          allStudentData = res;
          this.dataSource = new MatTableDataSource<any>(allStudentData);
          console.log(allStudentData);
        }
      })

    }
  }

  deleteConform(index?) {
    const dialogRef = this.dialog.open(DialogDeleteDialog, {
      width: '300px',
      minWidth: '250px',
      data: { data: allStudentData, index: index }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result == 'delete') {
        let cIndex = allStudentData[index];
        let value = JSON.parse(localStorage.user_data);
        let ref = this.db.collection('allschool').doc(value.country).collection(value.state).
          doc(value.district).collection(value.schoolName).doc('allClass').collection('Classes').
          doc(this.id).collection('students').doc(cIndex.payload.doc.id);
        ref.delete().then((res) => {
          console.log('delete');
        })
      }

    });
  }
  editStudentDetails(index) {
    this.openTeacherDialog('update', index);
  }
  openTeacherDialog(status?, index?) {
    const dialogRef = this.dialog.open(DialogAddStudentDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: allStudentData, id: this.id, status: status, ind: index }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
  }

  openStudentDetailsDialog(index) {
    let cData = allStudentData[index].payload.doc.data().info;
    console.log(cData);
    const dialogRef = this.dialog.open(DialogStudentDetailsDialog, {
      width: '90%',
      minWidth: '250px',
      data: { id: this.id, student: cData }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
  }




}
