import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DialogResultDialog } from 'src/app/dialog/addresult/result.dialog';

@Component({
  selector: 'app-studentresult',
  templateUrl: './studentresult.component.html',
  styleUrls: ['./studentresult.component.css']
})
export class StudentresultComponent implements OnInit {

  constructor(public dialog: MatDialog,) { }

  ngOnInit() {
  }

  
  openResultDialog(status?, index?) {
    const dialogRef = this.dialog.open(DialogResultDialog, {
      width: '90%',
      minWidth: '250px',
      data: { status: status }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
}
}
