import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { DialogAddPricipalDialog } from 'src/app/dialog/addprincipal/addprincipal.dialog';
import { AngularFirestore } from '@angular/fire/firestore';
import { DialogDeleteDialog } from 'src/app/dialog/deletebox/delete.dialog';
@Component({
  selector: 'app-principal',
  templateUrl: './principal.component.html',
  styleUrls: ['./principal.component.css']
})
export class PrincipalComponent implements OnInit {
  principalData: any;
  constructor(private dialog: MatDialog, private db: AngularFirestore) { }

  ngOnInit() {
    this.getPricipalData();
  }

  getPricipalData() {
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      let value = JSON.parse(localStorage.user_data);
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('principal');
      ref.snapshotChanges().subscribe((res: any) => {
        if (res && res.payload && res.payload.data()) {
          this.principalData = res.payload.data();
          console.log("principal", this.principalData);
        }
      })
    }
  }


  addPricipalDetails(status?, index?) {
    const dialogRef = this.dialog.open(DialogAddPricipalDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: this.principalData, ind: index, status: status }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed');

    });
  }
  deletePricipalDetails() {

    const dialogRef = this.dialog.open(DialogDeleteDialog, {
      width: '300px',
      minWidth: '250px',
      data: { totalData: this.principalData, }
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result == 'delete') {
        if (localStorage && localStorage.user_data && localStorage.user_data.length) {
          let value = JSON.parse(localStorage.user_data);
          let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('principal');
          ref.delete().then((res: any) => {
            console.log("principal");
          })
        }
      }

    });
  }

  principalAttendence(event) {
    console.log('toggle', event.checked);
    if (localStorage && localStorage.user_data && localStorage.user_data.length) {
      let value = JSON.parse(localStorage.user_data);
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('principal');
      this.principalData.isPresent = event.checked;
      ref.update(this.principalData).then((res: any) => {
        //  this.openSnackBar("status updated");
        console.log("Updated");
      });

    }
  }

}
