import { Component, Inject, ViewChild, AfterViewInit } from '@angular/core';
import { COMMA, ENTER } from '@angular/cdk/keycodes';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatChipInputEvent } from '@angular/material/chips';
import { FormGroup, FormBuilder, Validators, FormControl } from "@angular/forms";
import { AngularFirestore } from '@angular/fire/firestore';
import { MatSnackBar } from '@angular/material/snack-bar';

@Component({
  selector: 'add-principal',
  templateUrl: 'addprincipal.dialog.html',
  styleUrls: ['addprincipal.dialog.css']
})
export class DialogAddPricipalDialog implements AfterViewInit {
  visible = true;
  selectable = true;
  removable = true;
  addOnBlur = true;
  isDisable = false;
  @ViewChild('chipList', { static: true }) chipList;
  readonly separatorKeysCodes: number[] = [ENTER, COMMA];
  constructor(public dialogRef: MatDialogRef<DialogAddPricipalDialog>,
    @Inject(MAT_DIALOG_DATA) public data, public fb: FormBuilder, private db: AngularFirestore, private _snackBar: MatSnackBar) {

  }
  principalForm: FormGroup;
  ngOnInit(): void {
    this.isDisable = true;
    this.reactiveForm();
    if (this.data && this.data.totalData) {
      let cData = this.data.totalData;
      if (this.data.status == 'update') {
        this.principalForm = this.fb.group({
          name: [cData.name, [Validators.required]],
          email: [cData.email, [Validators.required]],
          mobileNumber: [cData.mobileNumber, [Validators.required]],
          gender: [cData.gender],
          dob: [cData.dob, [Validators.required]],
          exprience: [cData.exprience, [Validators.required]],
          thought: [cData.thought, [Validators.required]],
        })
      }
    }

  }
  ngAfterViewInit() {
  }
  /* Reactive form */
  reactiveForm() {
    this.principalForm = this.fb.group({
      name: ['', [Validators.required]],
      email: ['', [Validators.required]],
      mobileNumber: ['', [Validators.required]],
      gender: ['Male'],
      dob: ['', [Validators.required]],
      exprience: ['', [Validators.required]],
      thought: ['', [Validators.required]],
    })
  }
  /* Date */
  date(e) {
    var convertDate = new Date(e.target.value).toISOString().substring(0, 10);
    this.principalForm.get('dob').setValue(convertDate, {
      onlyself: true
    })
  }




  submitForm() {
    debugger;
    if (localStorage && localStorage.user_data && localStorage.user_data.length && this.principalForm.valid) {
      let value = JSON.parse(localStorage.user_data);
      let principalData: any = this.principalForm.value;
      var timeStamp = new Date().getTime();
      principalData.id = timeStamp.toString();
      principalData.isPresent = false;
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('principal');
      ref.get().subscribe((res) => {
        ref.set(principalData).then((res: any) => {
          this.openSnackBar("principal Added");
          this.onNoClick();
        });
      })
    }
  }
  deleteUpdateCurrentTeacher(event) {
    event.stopPropagation();
    event.preventDefault();
    if (localStorage && localStorage.user_data && localStorage.user_data.length && this.principalForm.valid) {
      let value = JSON.parse(localStorage.user_data);
      let principalData: any = this.principalForm.value;
      let ref = this.db.collection('allschool').doc(value.country).collection(value.state).doc(value.district).collection(value.schoolName).doc('principal');
      this.onNoClick();
      ref.set(principalData).then((res: any) => {
        if (this.data.status == 'update') {
          this.openSnackBar("Principal updated");
        }
      });

    }
  }
  openSnackBar(message: string, action?: string) {
    this._snackBar.open(message, action, {
      duration: 1000,
    });
  }
  onNoClick(): void {
    this.dialogRef.close();
  }

}